import Vue from 'vue'
import Router from 'vue-router'
import RanOut from 'components/RanOut'
import Pantry from 'components/Pantry'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Pantry',
      component: Pantry
    },
    {
      path: '/ranout',
      name: 'RanOut',
      component: RanOut
    }
  ]
})



